* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://cdn.shopify.com/s/files/1/1143/5434/products/PolkaDot_grey-on-white_D_for_web_18f86a98-4642-4c82-96e0-6c9d50f16a20.jpg?v=1615485577);
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  margin-left: 8px;
  padding: 0;
}

.country-list__item {
  display: flex;
  list-style: none;
  padding: 0;
  margin-bottom: 10px;
}
.country-list__flag {
  display: block;
  margin-right: 10px;
  width: 60px;
  height: 40px;
}

.country-info__item {
  list-style: none;
}
.country-info__thumb {
  display: flex;
}
.country-info__flag {
  display: block;
  margin: auto 0;
  margin-right: 10px;
  width: 60px;
  height: 40px;
}
.country-info__name {
  font-size: 25px;
}
